import React from 'react'
import { Avatar, Box, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Switch, Typography } from '@mui/material';
import { AccountBox, Article, Group, Home, ModeNight, Person, Settings, Storefront } from '@mui/icons-material';

const Sidebar = ({mode,setMode}) => {
  return (
    <Box
      flex={1} 
      p={2} 
      sx={{display:{xs:"none",sm:"block"}}}
      
      >
        <Box position={'fixed'}>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <Avatar
            sx={{ width: 40, height: 40, ml:1, mt:1}} 
            src="https://i.imgur.com/uehHFyO.jpeg"
          />
          <Typography variant='span' sx={{ ml: 3,mt:1 }}>Richard Hendricks</Typography>
        </div>
        <List>
        <ListItem disablePadding>
          <ListItemButton component="a" href="">
            <ListItemIcon>
              <Home color="success"/>
            </ListItemIcon>
            <ListItemText primary="Homepage" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton component="a" href="#group">
            <ListItemIcon>
              <Group color="success"/>
            </ListItemIcon>
            <ListItemText primary="Groups" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton component="a" href="#store">
            <ListItemIcon>
              <Storefront color="success"/>
            </ListItemIcon>
            <ListItemText primary="Marketplace" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton component="a" href="#person">
            <ListItemIcon>
              <Person color="success"/>
            </ListItemIcon>
            <ListItemText primary="Friends" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton component="a" href="#settings">
            <ListItemIcon>
              <Settings color="success"/>
            </ListItemIcon>
            <ListItemText primary="Settings" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton component="a" href="#profile">
            <ListItemIcon>
              <AccountBox color="success"/>
            </ListItemIcon>
            <ListItemText primary="Profile" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton component="a" href="#mode">
            <ListItemIcon>
              <ModeNight color="success"/>
            </ListItemIcon>
            <Switch color="success" onChange={e=>setMode(mode === "light" ? "dark" : "light")}/>
          </ListItemButton>
        </ListItem>
      </List>
        </Box>
      
    </Box>
  )
}
export default Sidebar;