import { Avatar, Box, Button, ButtonGroup, Fab, Modal, Stack, TextField, Tooltip,Typography,styled } from '@mui/material'
import React,{useState} from 'react'
import{Add as AddIcon, EmojiEmotions, PersonAdd, VideoCameraBack, Image, DateRange, AddLocationAlt, GifBox} from "@mui/icons-material"

const SytledModal = styled(Modal)({
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
});
const UserBox = styled(Box)({
    display: "flex",
    alignItems: "center",
    gap: "10px",
    marginBottom: "20px",
  });

const Add = () => {
    const [open, setOpen] = useState(false)
  return (
    <>
        <Tooltip onClick={e=>setOpen(true)}
            
            title="Add"
            sx={{
                position: "fixed",
                bottom: 20,
                left: { xs: "calc(50% - 25px)", md: 30 },
              }}
        
        >
            <Fab  color="success" aria-label="add">
                <AddIcon/>
            </Fab>
        </Tooltip>
        <SytledModal
            open={open}
            onClose={e=>setOpen(false)}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
            >
            <Box width={300} height={250} bgcolor={"background.default"} color={"text.primary"} p={3} borderRadius={5}>
                <Typography variant='h6' color="gray" textAlign="center">
                    Create post
                </Typography>
                <UserBox>
                    <Avatar 
                        src='https://i.imgur.com/uehHFyO.jpeg'
                        alt=''
                    />
                    <Typography fontWeight={500} variant='span'>Richard Hendricks</Typography>
                </UserBox>
                <TextField
                    sx={{ width:"100%"}}
                    id="standard-multiline-static"
                    multiline
                    rows={2}
                    placeholder=" What's on your mind? "
                    variant="standard"
                />
                <Stack direction="row" gap={1} mt={2} mb={3}>
                    <Image color="success"/>
                    <PersonAdd color="success"/>
                    <EmojiEmotions color='success'/>
                    <AddLocationAlt color="success"/>
                    <GifBox color="success" />
                    <VideoCameraBack color='success'/>
                </Stack>
                <ButtonGroup fullWidth variant="contained" aria-label="outLined primary button group">
                <Button color='success'>Post</Button>
                <Button color='success' sx={{width:"100px"}}><DateRange></DateRange></Button>
                </ButtonGroup>
            </Box>
        </SytledModal>
  </>
  )
}

export default Add