import { LocalParking, Mail, Notifications } from '@mui/icons-material';
import { AppBar, Menu, MenuItem, useTheme, Box, Badge, InputBase, styled, Toolbar, Typography, Avatar } from '@mui/material';
import React, { useState } from 'react';

const StyledToolbar = styled(Toolbar)({
  display: "flex",
  justifyContent: "space-between"
});

const Search = styled("div")(({ theme }) => ({
  backgroundColor: "white",
  padding: "0 10px",
  borderRadius: theme.shape.borderRadius,
  width: "40%"
}));

const Icons = styled(Box)(({ theme }) => ({
  display: "none",
  gap: "20px",
  alignItems: "center",
  [theme.breakpoints.up("sm")]: {
    display: "flex"
  }
}));

const UserBox = styled(Box)(({ theme }) => ({
  display: "flex",
  gap: "10px",
  alignItems: "center",
  [theme.breakpoints.up("sm")]: {
    display: "none"
  }
}));

const Navbar = ({ mode, setMode }) => {
  const [open, setOpen] = useState(false);
  const theme = useTheme();

  const handleModeSwitch = () => {
    setMode(mode === "light" ? "dark" : "light");
    setOpen(false); // Close the menu after mode switch
  };

  return (
    <AppBar position='sticky' color="success">
      <StyledToolbar>
        <Typography variant="h6" sx={{ display: { xs: "none", sm: "block" } }}>Pied Piper</Typography>
        <LocalParking color="#f1f8e9" sx={{ display: { xs: "block", sm: "none" } }} />
        <Search>
          <InputBase placeholder="search..." />
        </Search>
        <Icons>
          <Badge badgeContent={4} color="error">
            <Mail color="action" />
          </Badge>
          <Badge badgeContent={2} color="error">
            <Notifications />
          </Badge>
          <Avatar
            sx={{ width: 40, height: 40 }}
            src="https://i.imgur.com/uehHFyO.jpeg"
          />
        </Icons>
        <UserBox onClick={e => setOpen(true)}>
          <Avatar
            sx={{ width: 30, height: 30 }}
            src="https://i.imgur.com/uehHFyO.jpeg"
          />
        </UserBox>
      </StyledToolbar>
      <Menu
        id="demo-positioned-menu"
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={open}
        onClose={() => setOpen(false)}
      >
        <MenuItem>Menu</MenuItem>
        <MenuItem>Lastes Stories</MenuItem>
        <MenuItem onClick={handleModeSwitch}>Switch Mode</MenuItem>
      </Menu>
    </AppBar>
  );
};

export default Navbar;
